import { CashIcon, UserIcon } from "@heroicons/react/outline";
import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../../AuthContext";
import CenterWhiteContainer from "../../components/Containers/CenterWhiteContainer";

const Dashboard = () => {
  const { user } = useContext(AuthContext);
  return (
    <CenterWhiteContainer>
      <div className=" w-full sm:p-5 grid gap-5">
        <div className=" grid gap-10 text-indigo-900">
          <h1 className=" text-3xl text-center font-semibold capitalize ">
            Welcome {`${user?.name} ${user?.surname}!`}
          </h1>
          <h1 className=" text-xl text-center ">
            What do you want to do today?
          </h1>
        </div>
        <div className="flex flex-col sm:flex-row gap-5">
          <div className="p-3  border-2 rounded-lg hover:scale-105 hover:shadow-xl md:ease-in duration-100 transform transition-all bg-indigo-50 justify-center items-center flex flex-col">
            <CashIcon className="sm:w-10/12 w-9/12 mx-auto text-indigo-900" />
            <Link
              to="/operations"
              className="py-2 px-4 bg-indigo-500 text-white rounded-md shadow-md hover:bg-indigo-700 cursor-pointer"
            >
              See my operations
            </Link>
          </div>
          <div className="p-3  border-2 rounded-lg hover:scale-105 hover:shadow-xl md:ease-in duration-100 transform transition-all bg-indigo-50 justify-center items-center flex flex-col">
            <UserIcon className="sm:w-10/12 w-9/12 mx-auto text-indigo-900" />
            <Link
              to="/profile"
              className="py-2 px-4 bg-indigo-500 text-white rounded-md shadow-md hover:bg-indigo-700 cursor-pointer"
            >
              See my profile
            </Link>
          </div>
        </div>
      </div>
    </CenterWhiteContainer>
  );
};

export default Dashboard;
