function TableLayout(
    {children}
  ) {
    return (
      <div className="hidden sm:block">
      <div className="-my-2 overflow-x-auto block">
        <div className="align-middle  sm:px-4 lg:px-8">
          <div className="my-1 p-3 transform hover:translate-x-1 flex duration-300 ease-in-out">
            <table className=" text-xs sm:text-lg table-auto text-gray-900 ">
              {children}
            </table>
          </div>
        </div>
      </div>
      </div>
    );
  }

  export default TableLayout;