import FormUserResetPass from "../../components/Forms/FormUserResetPass";
import { resetPassword } from "../../services/resetPassword";
import { useHistory } from "react-router-dom";
import { useState } from "react";
import ModalWindow from "../../components/Modals/ModalWindow";
import CenterContainer from "../../components/Containers/CenterContainer";

const ResetPass = () => {
  const [open, setOpen] = useState(false);
  const [error, setError] = useState("");
  const history = useHistory();

  const handleResetPass = async (values, { setFieldError }) => {
    console.log(values)
    try {
      if (values.password === values.passwordRepeated) {
        const { data } = await resetPassword(values);
        if (!data.errors) {
          setOpen(true);
        } else {
          setError(data.errors);
        }
      } else {
        setFieldError("passwordRepeated", "The passwords must be the sames");
      }
    } catch {
      console.log("error");
    }
  };

  const handleConfirm = () => {
    setOpen(false);
    history.push("/login");
  };
  return (
    <CenterContainer>
      <FormUserResetPass
        onSubmit={handleResetPass}
        error={error}
      />
      <ModalWindow
        modalText={"Your account has been updated successfully!"}
        open={open}
        setOpen={open}
        handleConfirm={handleConfirm}
        type="confirm"
      />
    </CenterContainer>
  );
};

export default ResetPass;
